.nav {
  background: #333;
}
button {
  background: #333 !important;
  margin-bottom: 20px;
  width: 100%;
  padding: 16px;
  font-size: 1.6em;
  color: #fff;
  border: 0;
  border-radius: 4px;
  cursor: pointer;
}
button.active {
  background: #00bfa5 !important;
}
form span {
  color: #00bfa5 !important;
}

.x-axis path,
.y-axis path,
.x-axis line,
.y-axis line {
  stroke: #ccc;
}
.x-axis text,
.y-axis text {
  fill: #ccc;
  font-size: 14px;
}
